import React, { Component } from 'react'
import { Card, Button, Icon, Radio } from 'antd'
import './index.less'
class Buttons extends Component {
    state = {
        loading: false,
        icon_loading: false,
        size:'small'
    }
    enterLoading = () => {
        this.setState({
            loading: true
        })
    }
    enterIconLoading = () => {
        this.setState({
            icon_loading: true
        })
    }
    loadingOff = () => {
        this.setState({
            loading: false,
            icon_loading: false
        })
    }
    handleSizeChange = (e)=>{
        this.setState({
            size: e.target.value
        })
    }
    render() {
        const { loading,icon_loading,size } = this.state
        return (
            <div>
                <Card title='' className='card'>
                    <Button type='primary'></Button>
                    <Button></Button>
                    <Button type='dashed'></Button>
                    <Button type='danger'></Button>
                    <Button disabled></Button>
                </Card>
                <Card title='' className='card'>
                    <Button icon='plus'></Button>
                    <Button icon='edit'></Button>
                    <Button icon='delete'></Button>
                    <Button shape='circle' icon='search' />
                    <Button icon='search' type='primary'></Button>
                    <Button icon='download' type='primary'></Button>
                </Card>
                <Card title='Loading' className='card'>
                    <Button loading type='primary'></Button>
                    <Button shape='circle' loading type='primary' />
                    <Button loading={loading} onClick={this.enterLoading} ></Button>
                    <Button loading={icon_loading} onClick={this.enterIconLoading} icon='poweroff'></Button>
                    <Button shape='circle' loading />
                    <Button type='primary' onClick={this.loadingOff}></Button>
                </Card>
                <Card title='' className='card0'>
                    <Button.Group>
                        <Button type="primary"><Icon type="left" /></Button>
                        <Button type="primary"><Icon type="right" /></Button>
                    </Button.Group>
                </Card>
                <Card title='' className='card'>
                    <Radio.Group value={size} onChange={this.handleSizeChange}>
                        <Radio value='small'></Radio>
                        <Radio value='default'></Radio>
                        <Radio value='large'></Radio>
                    </Radio.Group>
                    <Button type='primary' size={size}></Button>
                    <Button size={size}></Button>
                    <Button type='dashed' size={size}></Button>
                    <Button type='danger' size={size}></Button>
                    <Button size={size} disabled></Button>
                </Card>
            </div>
        )
    }
}

export default Buttons