import React, { Component } from 'react'
import { Card, Table, Button, Modal, message } from 'antd';
import './index.less'
import { tableList } from '../../../api/table'
import Utils from '../../../utils/utils'
const { pagination } = Utils
const { confirm } = Modal
const { success } = message
class BasicTable extends Component {
    params = {
        page: 1
    }
    componentWillMount() {
        const dataSource = [
            {
                "id": 1,
                "name": "",
                "gender": 2,
                "status": 4,
                "age": 57,
                "address": "  "
            },
            {
                "id": 2,
                "name": "",
                "gender": 2,
                "status": 4,
                "age": 57,
                "address": "  "
            },
        ];
        dataSource.map((item, index) => item.key = index)
        this.setState({ dataSource })
        this.requestData()
    }
    requestData = async () => {
        const { data, status } = await tableList(this.params.page)
        const { code, result } = data
        const { list } = result
        if (status === 200 && code === 0) {
            list.map((item, index) => item.key = index)
            this.setState({
                list, pagination: pagination(result, (current) => {
                    this.params.page = current
                    this.requestData()
            }) })
        }
    }
    handleRowClick = (selectRowItem, index) => {
        const selectedRowKeys = [index]
        this.setState({ selectRowItem, selectedRowKeys })
    }
    handleRowsDel = () => {
        let { selectRowList } = this.state
        const ids = selectRowList && selectRowList.map(item => item.id)
        ids && confirm({
            content: `:${ids}`,
            onOk: () => success('!'),
            onCancel() { },
        });

    }
    render() {
        const { dataSource, list, selectedRowKeys, selectedCheckRowKeys, pagination } = this.state
        const columns = [
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
            },
            {
                title: '',
                dataIndex: 'gender',
                key: 'gender',
                render: (gender) => {
                    return gender === 1 ? '' : ''
                }
            },
            {
                title: '',
                dataIndex: 'status',
                key: 'status',
                render: (status) => {
                    let conf = {
                        1: '',
                        2: '',
                        3: '',
                        4: '',
                        5: ''
                    }
                    return conf[status]
                }
            },
            {
                title: '',
                dataIndex: 'age',
                key: 'age',
            },
            {
                title: '',
                dataIndex: 'address',
                key: 'address',
            },
        ];
        const rowSelection = {
            type: 'radio',
            selectedRowKeys
        }
        const rowCheckSelection = {
            type: 'checkbox',
            selectedRowKeys: selectedCheckRowKeys,
            onChange: (selectedCheckRowKeys, selectRowList) => {
                this.setState({ selectedCheckRowKeys, selectRowList })
            }
        }
        return (
            <div>
                <Card title='' className='card'>
                    <Table dataSource={dataSource} columns={columns} bordered />
                </Card>
                <Card title='-Mock' className='card'>
                    <Table dataSource={list} columns={columns} bordered />
                </Card>
                <Card title='' className='card'>
                    <Table
                        dataSource={list}
                        columns={columns}
                        bordered
                        rowSelection={rowSelection}
                        onRow={(record, index) => {
                            return {
                                onClick: event => this.handleRowClick(record, index), // 
                            };
                        }}
                    />
                </Card>
                <Card title='' className='card'>
                    <Button type='primary' onClick={this.handleRowsDel}></Button>
                    <Table
                        dataSource={list}
                        columns={columns}
                        bordered
                        rowSelection={rowCheckSelection}
                    />
                </Card>
                <Card title='' className='card'>
                    <Table
                        dataSource={list}
                        columns={columns}
                        bordered
                        pagination={pagination}
                    />
                </Card>
            </div>
        )
    }
}

export default BasicTable