import React, { Component } from 'react'
import { Card, Table, Button, Modal, message } from 'antd'
import './index.less'
import FilterForm from './component/FilterForm'
import OpenCityForm from './component/OpenCityForm'
import { cityList } from '../../api/city'
import Utils from '../../utils/utils'
import { openCity } from '../../api/city'

const { pagination } = Utils

class City extends Component {
    state = {
        list: []
    }
    params = {
        page: 1
    }
    componentDidMount() {
        this.requestData()
    }
    requestData = async () => {
        const { page } = this.params
        const { result } = await cityList(page)
        let { list } = result
        list = list.map((item, index) => {
            item.key = index
            item.city_admins = item.city_admins.map(item => item.user_name).join('')
            item.car_mode = item.car_mode === 1 ? '' : ''
            item.opt_mode = item.opt_mode === 1 ? '' : ''
            return item
        })
        this.setState({
            list,
            pagination: pagination(result, current => {
                this.params.page = current
                this.requestData()
            })
        })
    }
    handleOpenCity = () => {
        this.setState({ isShowOpenCity: true })
    }
    handleCancel = () => {
        this.setState({ isShowOpenCity: false })
    }
    handleSubmit = () => {
        const { validateFields } = this.form_data.props.form
        validateFields(async (err, data) => {
            if (!err) {
                const {code,result} = await openCity(data)
                if(code === 0) {
                    message.success(result.message)
                    this.setState({ isShowOpenCity: false })
                }
            }
        })
    }
    render() {
        let colums = [
            { title: 'ID', dataIndex: 'city_id' },
            { title: '', dataIndex: 'city_name' },
            { title: '', dataIndex: 'car_mode' },
            { title: '', dataIndex: 'opt_mode' },
            { title: '', dataIndex: 'auth_user' },
            { title: '', dataIndex: 'city_admins' },
            { title: '', dataIndex: 'city_open_date' },
            { title: '', dataIndex: 'update_date' },
            { title: '', dataIndex: 'option_person' }
        ]
        colums = colums.map((item, index) => {
            item.key = index
            return item
        })
        const { list, pagination, isShowOpenCity } = this.state
        return (
            <div>
                <Card className='card'>
                    <FilterForm />
                </Card>
                <Card className='card'>
                    <Button type='primary' onClick={this.handleOpenCity}></Button>
                    <Table columns={colums} dataSource={list} pagination={pagination} bordered></Table>
                </Card>
                <Modal
                    title=''
                    visible={isShowOpenCity}
                    onOk={this.handleSubmit}
                    onCancel={this.handleCancel}
                    cancelText=''
                    okText=''
                >
                    <OpenCityForm wrappedComponentRef={(instance) => { this.form_data = instance }} />
                </Modal>
            </div>
        )
    }
}

export default City